import React from 'react';
import ReactEcharts from "echarts-for-react";
import echarts from "echarts";
import * as mobx from 'mobx';

export default class ProductCycle extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
            option:null,
            data:this.props.data
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.data != this.props.data) {
           this.setState({data:nextProps.data})
        }
    }

    componentWillMount() {

    }

    getRateByName(name) {
        let result="";
        const {data}= this.state;
        const dataList = data.filter((item)=>{
            if(item.name===name){
               return item
            }
        })
        if(dataList.length>0) {
            let tempData = (dataList[0]['rate']*100).toFixed(2);
            result = tempData+'%';
        }
        return result;
    }

	render() {
        const labourRate = this.getRateByName('劳务仓');
        // const labourRate = "22.5%";
        const hallRate = this.getRateByName('展厅');
        const officeRate = this.getRateByName('综合办公');
        var scale = 1;
        var rich = {
            name: {
                fontSize: 12 * scale,
                padding: [-25,5,5,5],
                align: 'center',
                fontFamily: '"Microsoft Yahei","微软雅黑"',
                color: "rgba(102, 102, 102, 0.847058823529412)"
            },
            rate: {
                color: "#000",
                fontSize: 12 * scale,
                align: 'center',
                padding: [-50, 0, 0, 0],
            }
        };
        var option = {
            backgroundColor: '#fff',
            series: [{
                    name: '劳务仓',
                    type: 'pie',
                    radius: ['46%', '58%'],
                    center: ['15%', '50%'],
                    color: '#56a0f8',
                    selectedOffset: 5,
                    grid: {
                        left: '5px',
                        right: '5px',
                        containLabel: true
                    },
                    label: {
                        normal: {
                            position: 'center'
                        }
                    },
                    data: [{
                        value: 25,
                        name: '劳务仓',
                        tooltip: {
                            show: false
                        },
                        selected: true,
                        label: {
                            normal: {
                                formatter: function(params, callback) {
                                    return '{name|' + '劳务仓' + '}\n\n{rate|' + labourRate + '}';
                                },
                                rich: rich,
                                textStyle: {
                                    fontSize: 12,
                                    color: '#000',
                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#56a0f8'
                            },
                            emphasis: {
                                color: '#56a0f8'
                            }
                        },
                    }, {
                        value: 75,
                        name: '',
                        label: {
                            show: false
                        },
                        tooltip: {
                            show: false
                        },
                        itemStyle: {
                            normal: {
                                color: '#f0f2f5'
                            },
                            emphasis: {
                                color: '#f0f2f5'
                            }
                        },
                    }]
                },

                {
                    name: '展厅',
                    type: 'pie',
                    radius: ['46%', '58%'],
                    center: ['50%', '50%'],
                    color: '#84d090',
                    selectedOffset: 5,
                    grid: {
                        left: '5px',
                        right: '5px',
                        containLabel: true
                    },
                    label: {
                        normal: {
                            position: 'center'
                        }
                    },
                    data: [{
                        value: 25,
                        name: '展厅',
                        tooltip: {
                            show: false
                        },
                        selected: true,
                        label: {
                            normal: {
                                formatter: function(params, callback) {
                                    return '{name|' + '展厅' + '}\n\n{rate|' + hallRate + '}';
                                },
                                rich: rich,
                                textStyle: {
                                    fontSize: 14,
                                    color: '#000',

                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#84d090'
                            },
                            emphasis: {
                                color: '#84d090'
                            }
                        },
                    }, {
                        value: 75,
                        name: '',
                        tooltip: {
                            show: false
                        },
                        itemStyle: {
                            normal: {
                                color: '#f0f2f5'
                            },
                            emphasis: {
                                color: '#f0f2f5'
                            }
                        },
                    }]
                },

                {
                    name: '综合办公',
                    type: 'pie',
                    radius: ['46%', '58%'],
                    center: ['85%', '50%'],
                    color: '#f6da6d',
                    selectedOffset: 5,
                    grid: {
                        left: '5px',
                        right: '5px',
                        containLabel: true
                    },
                    label: {
                        normal: {
                            position: 'center'
                        }
                    },
                    data: [{
                        value: 25,
                        name: '综合办公',
                        tooltip: {
                            show: false
                        },
                        selected: true,
                        label: {
                            normal: {
                                formatter: function(params, callback) {
                                    return '{name|' + '综合办公' + '}\n\n{rate|' + officeRate + '}';
                                },
                                rich: rich,
                                textStyle: {
                                    fontSize: 14,
                                    color: '#000',

                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#f6da6d'
                            },
                            emphasis: {
                                color: '#f6da6d'
                            }
                        },

                    }, {
                        value: 75,
                        name: '',
                        tooltip: {
                            show: false
                        },
                        itemStyle: {
                            normal: {
                                color: '#f0f2f5'
                            },
                            emphasis: {
                                color: '#f0f2f5'
                            }
                        },
                    }]
                }
            ]
        };
		return (
            <ReactEcharts
                option={option}
                style={{width: '100%', height:'100%'}}
                notMerge={true}
                lazyUpdate={true}
            />
		)
	}
}



