import React from 'react';
import { Typography } from 'antd';

const { Paragraph } = Typography;
export default class Top5List extends React.Component {
	constructor(props) {
		super(props);
        this.state = {
            option:null,
            data:this.props.data
        }

    }


    componentWillReceiveProps(nextProps) {
        if(nextProps.data != this.props.data) {
           this.setState({data:nextProps.data})
        }
    }

    render() {
        const { data } = this.state;
		return (
			<div className="top_5" style={{height: '100%'}}>
                    <ul className="clearfix w">
                        {data.map((item, index)=>{
                            return (<li key={index} className="w">
	                                <a className="fr"
	                                   title={item.amount+"万"}
	                                   className="fr"
	                                   style={{
		                                   maxWidth:'100px',
		                                   whiteSpace: 'nowrap',
		                                   textOverflow: 'ellipsis',
		                                   overflow: 'hidden'
	                                   }}
	                                >{item.amount}万</a>
		                            <a  title={item.name}
			                            className="fl"
			                            style={{
		                            	width: 'calc(100% - 130px)',
			                            whiteSpace: 'nowrap',
			                            textOverflow: 'ellipsis',
			                            overflow: 'hidden'
		                            }}>{item.name}</a>
	                               </li>)
                        })}
                    </ul>
			</div>
		)
	}
}




