import React from 'react';
import {Row, Col, Card, Typography} from 'antd';
import {myBrowser, isScroll} from '@/utils/utils';
import {withRouter} from 'react-router-dom';
import * as  mobx from 'mobx';

const {Paragraph} = Typography;

@withRouter
export default class Progress extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
	}

	componentWillReciveProps(nextProps) {

	}

	componentDidMount() {

	}

	makeHeightEqual = (data) => {
		if (!this.store.projectBoardListDone) return;
		try {
			let progresscontentDivClientHeight = document.getElementById('progresscontent').clientHeight;
			let stagesList = data.map(item => {
				return {"stageId": item.stageId, "projectNum": item.projectList.length};
			})
			stagesList.shift();
			stagesList = stagesList.filter(item => item.stageId !== '8'&&item.stageId !== '9');

			let stageNames = stagesList.map(item => {
				return item.stageId
			});
			let projectNums = stagesList.map(item => {
				return parseInt(item.projectNum)
			});
			let maxNum = Math.max(...projectNums);
			let maxStageName = stageNames[projectNums.findIndex(value => value == maxNum)];
			let otherStageNames = stageNames.filter(stageName => maxStageName !== stageName);
			// alert(otherStageNames.filter(stageName => document.getElementById(stageName)==null).length)
			let maxStageDivClientHeight = document.getElementById('stage' + maxStageName).clientHeight;

			let maxHeight = maxStageDivClientHeight < progresscontentDivClientHeight ? progresscontentDivClientHeight : maxStageDivClientHeight;

			otherStageNames.forEach(item => {
				document.getElementById('stage' + item).style.height = maxHeight + 'px';
			})
			document.getElementById('stage' + maxStageName).style.height = maxStageDivClientHeight < progresscontentDivClientHeight ? maxHeight + 'px' : (maxHeight + 1) + 'px';


		} catch (error) {

		}

	}

	jumpProjectDetails(project) {
		const urlObj = {1: 'workdetail', 2: 'labor', 3: 'productdetail', 4: 'exhibition', 5: 'exhibition'};
		const {projectType, id} = mobx.toJS(project);
		const address = urlObj[projectType];
		const linkTo = `/bpm/${address}?projectid=${id}&temId=${projectType}`
		this.props.history.push(linkTo);
	}

	render() {
		const {data} = this.props;
		//console.log(mobx.toJS(data),'data')

		let sData = Object.assign([], mobx.toJS(this.store.projectStage));
		let ppData = null;

		ppData = sData.map((itemStage, indexStage) => {
			let projectList = []
			//console.log(itemStage.stageId);
			data.forEach((itemProject, indexProject) => {
				if (itemStage.stageId === itemProject.stageId) {
					projectList.push(itemProject)
				}
				// 将stageId==10的数据合并到stageId==6中
				if (itemProject.stageId==10) {
					let obj = Object.assign(itemProject, {stageId: '6'})
					projectList.push(obj)
				}
			})
			return Object.assign({}, itemStage, {"projectList": projectList});
		})
		this.makeHeightEqual(ppData);
		return (
			<div className="h">
				<div className={'bpg_progress_header'}
				     className={myBrowser() !== 'Chrome' && myBrowser() != 'Safari' ? 'bpg_progress_header_mr17' : 'bpg_progress_header'}>
					{
						ppData.map((item, index) => {
							if (item.name != "项目信息" && item.name != "产品设计" && item.name != "项目评估" && item.name != "实施交付") {
								return (
									<Col xs={4} sm={4} md={4} lg={4} xl={4} key={'list2' + index}>
										<Card bordered={true} style={{maxWidth: '200px', backgroundColor: '#ccc'}}
										      className="mr20 kanban_cardtwo">
											<h3 className="bpm_progress_title" title={item.name}>{item.name} ({item.projectList.length})</h3>
										</Card>
									</Col>
								)
							}
						})
					}
				</div>
				<div id="progresscontent"
				     className={myBrowser() !== 'Chrome' && myBrowser() != 'Safari' ? 'bpg_progress_body_ff' : 'bpg_progress_body'}>
					{
						ppData.map((item, index) => {
							if (item.name != "项目信息" && item.name != "产品设计" && item.name != "项目评估" && item.name != "实施交付") {
								return (
									<Col xs={4} sm={4} md={4} lg={4} xl={4} key={'list' + index}>
										<Card id={`stage${item.stageId}`} bordered={true}
										      className="mr20  kanban_cardone"
										      style={{maxWidth: '200px', paddingTop: '10px', backgroundColor: '#ccc'}}>
											{
												item.projectList.map((itemProject, indexProject) => {
													return (
														<div title={itemProject.name}
														     className="bpm_progress_item item_1" onClick={(e) => {
															this.jumpProjectDetails(itemProject)
														}}>
															<span
																className={itemProject.currentTaskState == 0 ? 'trangle1' : itemProject.currentTaskState == 1 ? 'trangle4' : itemProject.currentTaskState == 2 ? 'trangle3' : itemProject.currentTaskState == 3 ? 'trangle2' : 'trangle4'}></span>
															<span
																className={itemProject.currentProgress < 10 ? 'trangle_label3' : itemProject.currentProgress == 100 ? "trangle_label" : "trangle_label2"}>{itemProject.currentProgress + '%'}</span>
															<p className="p1 mt20">
																<Paragraph ellipsis>
																	{itemProject.name}
																</Paragraph>
															</p>
															<p className="p2">
																<Paragraph ellipsis>
																	{'(' + itemProject.code + ')'}
																</Paragraph>
															</p>
														</div>
													)
												})
											}
										</Card>
									</Col>
								)
							}
						})
					}
				</div>
			</div>
		)
	}
}



