import React from 'react';
import {Icon, Table, Pagination, ConfigProvider, Menu, Checkbox, Input, Button} from 'antd';
//import InfiniteScroll from 'react-infinite-scroller';
import * as mobx from 'mobx';
import {withRouter} from 'react-router-dom';
import {Scrollbars} from 'react-custom-scrollbars';

const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);
@withRouter
export default class DetailsList extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			tableHeight: document.getElementById('tabpane').clientHeight - 100,
			products: [],
			state: [],
			masterIds: [],
		}
	}

	changeHeight = () => {
		let tabpaneDivClientHeight = document.getElementById('tabpane').clientHeight;
		this.setState({tableHeight: tabpaneDivClientHeight - 100})
	}

	componentDidMount() {
		window.addEventListener('resize', this.changeHeight)
		this.setState({
			products: [],
			state: [],
			masterIds:[]
		});
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.changeHeight)
	}

	onShowSizeChange = (current, pageSize) => {
		this.store.allProjectParams = Object.assign(mobx.toJS(this.store.allProjectParams), {
			pageIndex: current,
			pageSize: pageSize
		});
		this.store.getAllProjectList();
	}

	onChange = (pageNumber) => {
		this.store.allProjectParams = Object.assign(mobx.toJS(this.store.allProjectParams), {
			pageIndex: pageNumber
		});
		this.store.getAllProjectList();
	}

	/*showRunStatus = (text, record) => {
		let color = {"color": "#0991FF", "cursor": "pointer"};
		if (text === "中途关闭") color = {"color": "#ff8c00", "cursor": "pointer"};
		if (text === "全部完成") color = {"color": "#00aa00", "cursor": "pointer"};
		return (<span onClick={() => {
		}} style={color}>{text}</span>)
	}*/

	getProjectStage = (text, record) => {
		let label = "";
		const {projectStage} = this.store;
		let color = {"color": "#0991FF", "cursor": "pointer"};
		if (record.state == 0) {
			label = projectStage.find((obj) => obj.stageId === record.stageId) ? projectStage.find((obj) => obj.stageId === record.stageId).name : ""
		} else if (record.state == 1) {
			label = "全部完成";
			color = {"color": "#00aa00", "cursor": "pointer"};
		} else if (record.state == 2) {
			label = "中途关闭";
			color = {"color": "#ff8c00", "cursor": "pointer"};
		}
		return (<span onClick={
			(e) => {
				this.pustLink(record);
			}
		} style={color}>{label}</span>)
	}

	pustLink = (record) => {
		// 根据projectType跳转
		const urlObj = {1: 'workdetail', 2: 'labor', 3: 'productdetail', 4: 'exhibition', 5: 'exhibition'};
		const {projectType, id, stageId} = record;
		const address = urlObj[projectType];
		const linkTo = `/bpm/${address}?projectid=${id}&temId=${projectType}&stageid=${stageId}`
		this.props.history.push(linkTo);
	}

	getProjectStageLabel = (record) => {
		let label = "";
		const {projectStage} = this.store;
		let color = {"color": "#0991FF", "cursor": "pointer"};
		if (record.state == 0) {
			label = projectStage.find((obj) => obj.stageId === record.stageId).name
		} else if (record.state == 1) {
			label = "全部完成";
			color = {"color": "#00aa00", "cursor": "pointer"};
		} else if (record.state == 2) {
			label = "中途关闭";
			color = {"color": "#ff8c00", "cursor": "pointer"};
		}
		return label;
	}

	showMilepostOrCloseReason = (text, record) => {
		let label = record.milepost;
		if (record.state == 2) {
			label = record.closeReason
		}
		return label;
	}

	// 设置状态
	onChangeState = (type, item, e) => {
		let newData = JSON.parse(JSON.stringify(this.state[type]));
		let index = newData.findIndex(vitem=>vitem==item.id);
		const {checked} = e.target;
		if (index==-1) {  // 新增
			this.setState({
				[type]:  [...newData, item.id]
			});
		} else {
			if (checked==false) { // 取消选中
				newData.splice(index, 1);
				this.setState({
					[type]: Object.assign([], newData)
				});
			}
		}
	}

	getColumnSearchProps = dataIndex => ({
		filterDropdown: ({ confirm, clearFilters }) => (
			<div style={{ padding: 8 }}>
				<Scrollbars
					autoHide
					autoHideTimeout={1000}
					autoHideDuration={200}
					autoHeight
					autoHeightMin={0}
					autoHeightMax={'310px'}
					thumbMinSize={30}
					universal={true}>
					{mobx.toJS(this.store[dataIndex]).map((item,index)=>{
						return (<div className="pl10"><Checkbox
							className="db"
							checked={this.state[dataIndex].includes(item.id)}
							key={dataIndex+"-"+index}
							onChange={this.onChangeState.bind(this, dataIndex, item)}>{item.typeName}</Checkbox></div>)
					})}
				</Scrollbars>
				<Button
					type="primary"
					onClick={() => this.handleSearch( confirm, dataIndex)}
					size="small"
					style={{ width: 60, marginRight: 8, marginTop: 8 }}
				>
					确定
				</Button>
				<Button onClick={() => this.handleReset(clearFilters, dataIndex)} size="small" style={{ width: 60, marginTop: 8  }}>
					重置
				</Button>
			</div>
		),
		filterIcon: filtered => (
			<Icon className="bocfiterIcon" type="caret-down" style={{color: filtered ? '#1890ff' : undefined}}/>
		),
		onFilter: (value, record) =>{
			/*record[dataIndex]
			.toString()
			.toLowerCase()
			.includes(value.toLowerCase())*/
		},
		onFilterDropdownVisibleChange: visible => {
			if (visible) {

			}
		},
		render: (record, text) => {
            // console.log(mobx.toJS(text), mobx.toJS(record),  dataIndex, '---')
			if (dataIndex=="products") {
				const val = mobx.toJS(text)["productType"];
				return typeof (val) !== 'undefined' ? val.join('、') : ''
			} else if (dataIndex=="state") {
				//const val = mobx.toJS(text)["projectState"];
				return this.getProjectStage("状态", mobx.toJS(text))
				//return typeof (val) !== 'undefined' ? val.join('、') : ''
			} else if (dataIndex=="masterIds") {
				const val = mobx.toJS(text)["masters"];
				return typeof (val) !== 'undefined' ? val.map((item) => {
					return item.userName
				}).join('、') : ''
			}
		}
	});

	// 确定
	handleSearch = (confirm, dataIndex) => {
		confirm();
		this.store.allProjectParams[dataIndex] = Object.assign([], this.state[dataIndex]);
		this.store.allProjectParams.pageIndex = 1;
		this.store.allProjectParams.pageSize = 10;
		this.store.getAllProjectList();
	};

	// 重置
	handleReset = (clearFilters,dataIndex)  => {
		clearFilters();
		this.store.allProjectParams[dataIndex] = Object.assign([]);
		this.store.allProjectParams.pageIndex = 1;
		this.store.allProjectParams.pageSize = 10;
		this.store.getAllProjectList();
		this.setState({
			[dataIndex]: Object.assign([])
		})
	};


	render() {
		const {totalCount, data} = this.props;
		/*const filterManagersMenu = (
			<Menu onClick={this.handleMenuClick}>
				<Menu.Item className="filter-menu-item " key="allmanagersmenu">
					<Checkbox className="ant-dropdown-menu-item" key="allmanagerscheck">全选</Checkbox>
				</Menu.Item>
				<Menu.Divider/>
			</Menu>
		)*/
		let columns = [
			{
				title: '项目编号', dataIndex: 'code', align: 'left'
			},
			{
				title: '项目名称', dataIndex: 'name', align: 'center', ellipsis: true,
				render: (text, record) => {
					return (<a onClick={(e) => {
						this.pustLink(record);
					}}>{text}</a>)
				}
			},
			{
				title: '产品类型', dataIndex: 'productType', align: 'center',
				//filters: mobx.toJS(this.store.productTyleList),
				...this.getColumnSearchProps('products'),
				/*filterIcon: filtered => (
					<Icon className="bocfiterIcon" type="caret-down" style={{color: filtered ? '#1890ff' : undefined}}/>
				),
				onFilter: (text, record) => {
					console.log(text, record, '筛选')
					//return record.productType.join('、').includes(text)
				},
				render: (text, record) => {
					return (<span onClick={(e) => {}}>{typeof (text) !== 'undefined' ? text.join('、') : ''}</span>)
				}*/
			},
			{
				title: '项目状态', dataIndex: 'projectState', align: 'center',
				...this.getColumnSearchProps('state'),
				/*filters: mobx.toJS(this.store.projectStatus),
				filterIcon: filtered => (
					<Icon className="bocfiterIcon" type="caret-down" style={{color: filtered ? '#1890ff' : undefined}}/>
				),
				onFilter: (text, record) => {
					return this.getProjectStageLabel(record) === text
				},
				render: (text, record) => {
					return this.getProjectStage(text, record)
				}*/
			},
			{
				title: '当前主负责人', dataIndex: 'masters', align: 'center',
				...this.getColumnSearchProps('masterIds'),
				/*filters: mobx.toJS(this.store.masterList),
				filterIcon: filtered => (
					<Icon className="bocfiterIcon" type="caret-down" style={{color: filtered ? '#1890ff' : undefined}}/>
				),
				onFilter: (text, record) => {
					return record.masters.map((item) => {
						return item.userName
					}).join('，').includes(text)
				},
				render: (text, record) => {
					return (<span onClick={(e) => {
					}}>{typeof (text) !== 'undefined' ? text.map((item) => {
						return item.userName
					}).join('，') : ''}</span>)
				}*/
			},
			{
				title: '关键里程碑', dataIndex: 'milepost', align: 'left', ellipsis: true,
				render: (text, record) => {
					return this.showMilepostOrCloseReason(text, record)
				}
			},
		];

		return (
			<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<Table
					className="m_table"
					columns={columns}
					dataSource={data}
					size="small"
					scroll={{y: ((this.state.tableHeight - 70)) + 'px'}}
					pagination={false}
				/>
				<Pagination
					className="mt20 tc"
					style={{
						position: 'absolute',
						left: '50%',
						width: '100%',
						transform: 'translateX(-50%)',
						bottom: '10px'
					}}
					onShowSizeChange={this.onShowSizeChange}
					onChange={this.onChange}
					total={totalCount}
					showQuickJumper
					// current={ activeKey==="0" ? mobx.toJS(this.store.params.pageIndex) : mobx.toJS(this.store.params2.pageIndex) }
					showTotal={total => `共 ${total} 条记录`}
				/>
			</ConfigProvider>
		)
	}
}




