import React from 'react';
import ReactEcharts from "echarts-for-react";
import echarts from "echarts";

export default class RateCharts extends React.Component {
	constructor(props) {
		super(props);
    }

    componentWillMount() {

    }

	render() {
        const {title, rateNum} = this.props;
        var option = option = {
            series: [{
                    name: '2019-02-12 任务分析',
                    type: 'gauge',
                    radius: '80%',
                    axisLine: { // 坐标轴线
                        lineStyle: { // 属性lineStyle控制线条样式
                            width: 15,
                            color: [
                                [0, '#539df6'],
                                [0.75, '#539df6'],
                                [1, '#539df6']
                            ]
                        }
                    },
                    splitLine: {
                        show: false,
                        length: 15,
                        lineStyle: {
                            //color:'black'
                        },
                    },
                    "axisTick": {
                        show: false,

                    },
                    axisLabel: {
                        formatter: function(e) {
                            switch (e + "") {
                                case "0":
                                    return "";
                                case "10":
                                    return "";
                                case "20":
                                    return "差";
                                case "30":
                                    return "";

                                case "40":
                                    return "中";
                                case "50":
                                    return "";

                                case "60":
                                    return "良";
                                case "70":
                                    return "";
                                case "80":
                                    return "优";
                                case "90":
                                    return "";
                                case "100":
                                    return "";
                                default:
                                    return e;
                            }
                        },
                        textStyle: {
                            fontSize: 14,
                            fontWeight: "",
                            color: '#000'
                        }
                    },
                    detail: {
                        formatter: '{value}%',
                        "textStyle": {
                            "fontSize": 20,
                            fontWeight: '700',
                            "color": '#000',
                        }
                    },
                    title: {
                       "fontSize": "12px",
                        color: '#999',
                        offsetCenter: [0, '50%']
                    },
                    pointer: {
                        show: true,
                        length: '60%',
                    },
                    itemStyle: { // 指针
                        color: '#539df6'
                    },
                    data: [{
                        value: rateNum,
                        name: title
                    }]
                },
                { //指针外环
                    "type": 'pie',
                    "hoverAnimation": false,
                    "legendHoverLink": false,
                    "radius": ['6%', '8%'],
                    "z": 10,
                    "label": {
                        "normal": {
                            "show": false
                        }
                    },
                    "labelLine": {
                        "normal": {
                            "show": false
                        }
                    },
                    "data": [{
                        "value": 80,
                        itemStyle: {
                            normal: {
                                color: "#539df6"
                            }
                        }
                    }]
                },
                { //指针内环
                    "type": 'pie',
                    "hoverAnimation": false,
                    "legendHoverLink": false,
                    "radius": ['0%', '6%'],
                    "z": 10,
                    "label": {
                        "normal": {
                            "show": false
                        }
                    },
                    "labelLine": {
                        "normal": {
                            "show": false
                        }
                    },
                    "data": [{
                        "value": 80,
                        itemStyle: {
                            normal: {
                                color: "#ffffff"
                            }
                        }
                    }]
                },
                {
                    name: '刻度尺', //刻度背景
                    type: 'gauge',
                    z: 2,
                    radius: '68%',
                    splitNumber: 4,
                    center: ["50%", "0%"], //整体的位置设置
                    axisLine: { // 坐标轴线
                        lineStyle: { // 属性lineStyle控制线条样式

                            width: 20,
                            opacity: 0, //刻度背景宽度
                        }
                    },
                    data: [{
                        show: false,
                        value: '100'
                    }],
                    axisLabel: {
                        show: false
                    },
                    "axisTick": {
                        show: false,

                    },
                    pointer: {
                        show: false
                    },
                    detail: {
                        show: 0
                    },
                    splitLine: {
                        length: 10, //刻度节点线长度
                        lineStyle: {
                            width: 3,
                            color: '#539df6'

                        } //刻度节点线
                    },
                },
            ]
        };
        const option2 = {  // echarts
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            grid: {
                left: '5px',
                right: '5px',
                containLabel: true
            },
            toolbox: {
                show : false,
                feature: {
                    restore: {},
                    saveAsImage: {}
                }
            },
            series: [
                {
                    name: '',
                    type: 'gauge',
                    detail: {formatter: '{value}%',textStyle:{fontSize:12},
                    },
                    radius: '100%',
                    data: [{value: rateNum, name: title}],
                    title: {
                        fontSize: "12px",
                        color: '#999',
                        offsetCenter: [0, '80%']
                    },
                    min:0,
                    max:100,    //设置刻度盘内数值区间
                    splitNumber:4,  //设置间隔区域的显示数量
                    axisLine: { // 坐标轴线
                        lineStyle: { // 属性lineStyle控制线条样式
                            width: 10,
                            color: [
                                [rateNum/100, "#5DD1FA"],   // 走过渐变色
                                [1, "#539df6"]
                            ]
                        },
                    },
                    // 分割线
                    splitLine: {
                        show: true,
                        length: 15,
                        lineStyle: {
                            color: "auto"
                        }
                    },
                    // 刻度线
                    axisTick: {
                        show: true,
                        length: 13,
                        splitNumber: 10,
                        lineStyle: {
                            color: "auto"
                        }
                    },
                    axisLabel: {
                        formatter:function(v){
                            switch (v + '') {
                                case '0' : return '';
                                case '100' : return '';
                                default:
                                    return v;
                            }
                        }
                    },
                    pointer: {
                        width: 7,//指针的宽度
                        length: "50%", //指针长度，按照半圆半径的百分比
                        shadowColor: '#ccc', //默认透明
                        shadowBlur: 5
                    },
                  }
                ]
            }
		return (
            <ReactEcharts
                style={{width: '100%', height:'100%',padding:'10px 10px'}}
                option={option2}
                notMerge={true}
                lazyUpdate={true}
            />
		)
	}
}



