/*
* 项目看板store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import Schemas from "@/utils/Schemas/bpm";
import moment from 'moment';
import emitter from "@/utils/events";
import authorizationSchemas from "@/utils/Schemas/authorization";
import bpmSchemas from "@/utils/Schemas/bpm";
import okrSchemas from "@/utils/Schemas/okr";

class BpmStore {
	@observable maxHeight=0;
	@observable projectBoardList = [];  // 项目看板列表(未完成)
	@observable projectBoardParams = {
		beginTime: moment().startOf('year').valueOf(),
		endTime:moment().endOf("year").valueOf(),
		text:''
	};
    @observable allProjectList = []; // 全部项目明细列表 state 完成100，关闭200 阶段1-7
    @observable allProjectParams = { //明细查询参数
		beginTime: moment().startOf('year').valueOf(),
		endTime:moment().endOf("year").valueOf(),
		text:'',
		products:[],
		state:[],
        masterIds:[],
        pageIndex:1,
		pageSize:10
	};
	@observable allProjectTotal = 0;
	@observable myTaskRunningNum = 0;
	@observable dealyWarnningList = []; //进度延迟预警列表

    @observable productProportion = []; //产品占比
    @observable topContractAmount = []; //项目订单排名
    @observable currentTaskList = []; //获取当前任务列表
    @observable currentTaskParams = { //获取当前任务列表参数 当前（state1延迟 2我的）任务列表(可以是主也可以是子任务 3 我的完成)
		state:1,
        pageIndex:1,
		pageSize:10
	};

	@observable dealyWarnningParams = { //获取当前任务列表参数 当前（state1延迟 2我的）任务列表(可以是主也可以是子任务 3 我的完成)
		state:1,
        pageIndex:1,
		pageSize:10
	};
	@observable hasMore = true;

	@observable currentTaskTotal = 0;
	//@observable unTaskTotal = 0;  // 未完成

    @observable deliverRate=null; //交付完成率
	@observable moneyBackRate=null; //回款完成率

	@observable projectStage=[  // 项目的不同阶段
		{"stageId":"1", "name":"项目信息"},
		{"stageId":"9", "name":"项目评估"},
		{"stageId":"2", "name":"方案设计"},
		{"stageId":"3", "name":"投标准备"},
		{"stageId":"4", "name":"合同签订"},
		{"stageId":"5", "name":"实施过程"},
		{"stageId":"6", "name":"交付验收"},
		{"stageId":"10", "name":"实施交付"},
		{"stageId":"7", "name":"开票收款"},
		{"stageId":"8", "name":"产品设计"},
	];

	@observable projectStatus=[  // 项目状态
		{"text":"项目信息", "value":"项目信息"},
		{"text":"项目评估", "value":"项目评估"},
		{"text":"方案设计", "value":"方案设计"},
		{"text":"投标准备", "value":"投标准备"},
		{"text":"合同签订", "value":"合同签订"},
		{"text":"实施过程", "value":"实施过程"},
		{"text":"交付验收", "value":"交付验收"},
		{"text":"实施交付", "value":"实施交付"},
		{"text":"开票收款", "value":"开票收款"},
		{"text":"全部完成", "value":"全部完成"},
		{"text":"产品设计", "value":"产品设计"},
		{"text":"中途关闭", "value":"中途关闭"},
	];

	@observable productTyleList = [];
	@observable masterList = [];
	@observable masterIds = [];
	@observable projectBoardListDone = false;


    // 项目看板列表(未完成)查询
	@action
	getProjectBoardList() {
		return requestPost('getProjectBoardList', Config.bpm.getProjectBoardList, Schemas.bpm.getProjectBoardList, this.projectBoardParams).then((res) => {
			this.projectBoardList = Object.assign([], res);
			this.projectBoardListDone = true;

		});
    }

    // 全部项目明细列表查询
	@action
	getAllProjectList() {
		return requestPost('getAllProjectList', Config.bpm.getAllProjectList, Schemas.bpm.getAllProjectList, this.allProjectParams).then((res) => {
			this.allProjectList =  Object.assign([], typeof(res.body)!=="undefined" ? res.body.map((item, index)=>{
				return item
			}):[]);
			this.allProjectParams.pageIndex = res.pageIndex;
			this.allProjectParams.pageSize = res.pageSize;
			this.allProjectTotal = res.totalCount;
		});
    }

    // 获取产品占比
	@action
	getProductProportion() {
		return requestPost('getProductProportion', Config.bpm.getProductProportion, Schemas.bpm.getProductProportion).then((res) => {
			this.productProportion = Object.assign([], res);
		});
    }

    // 获取项目订单排名
	@action
	getTopContractAmountList() {
		return requestPost('getTopContractAmountList', Config.bpm.getTopContractAmountList, Schemas.bpm.getTopContractAmountList).then((res) => {
			this.topContractAmount = Object.assign([], res);
		});
    }

    // 获取当前任务（state1延迟 2我的）任务列表(可以是主也可以是子任务 3 我的完成)
	@action
	getCurrentTaskList() {
		return requestPost('getCurrentTaskList', Config.bpm.getCurrentTaskList, Schemas.bpm.getCurrentTaskList, this.currentTaskParams).then((res) => {
			this.currentTaskList = Object.assign([], res.body);
			if(this.currentTaskParams.state==1) { //进度延迟预警
				this.dealyWarnningList = Object.assign([], res.body);
			}

			// if(this.currentTaskParams.state==2) {
			// 	emitter.emit("getMytaskNum", res.totalCount);
			// }

			this.currentTaskParams.pageIndex = res.pageIndex;
			this.currentTaskParams.pageSize = res.pageSize;
			this.currentTaskTotal = res.totalCount;
		});
	}

	// 获取当前任务（state1延迟 2我的）任务列表(可以是主也可以是子任务 3 我的完成)
	@action
	getWarnningList() {
		return requestPost('getCurrentTaskList', Config.bpm.getCurrentTaskList, Schemas.bpm.getCurrentTaskList, this.dealyWarnningParams).then((res) => {
			if (res && res['body'].length > 0) {
				let tempData = this.dealyWarnningList;
				this.dealyWarnningList = tempData.concat(res['body'])
				this.dealyWarnningParams.pageIndex = this.dealyWarnningParams.pageIndex+1;
			} else {
				this.hasMore = false;
			}

		});
	}

    // 获取进行中/已完成总数
    @action
    getListData(param) {
        return requestPost('getCurrentTaskList', Config.bpm.getCurrentTaskList, Schemas.bpm.getCurrentTaskList, param).then((res) => {
            return res;
        });
    }

    // 获取交付完成率
	@action
	getDeliverRate() {
		return requestPost('getDeliverRate', Config.bpm.getDeliverRate, Schemas.bpm.getDeliverRate).then((res) => {
			this.deliverRate = res;
		});
    }

    // 获取回款完成率
	@action
	getMoneyBackRate() {
		return requestPost('getMoneyBackRate', Config.bpm.getMoneyBackRate, Schemas.bpm.getMoneyBackRate).then((res) => {
			this.moneyBackRate = res;
		});
	}

	// 获取我的任务数
	@action
	getMyTaskCount() {
		return requestPost('getMyTaskCount', Config.bpm.getMyTaskCount, Schemas.bpm.getMyTaskCount).then((res) => {
			emitter.emit("getMytaskNum",  res);
		});
	}

	@observable products = [];
	// 获取产品类型
	@action
	getProductTypeList() {
		return requestPost('getProductTypeList', Config.bpm.getProductTypeList, Schemas.bpm.getProductTypeList).then((res) => {
			this.productTyleList = Object.assign([], res.map(item=>{
				return {text:item.typeName, value:item.typeName}
			}));
			this.products = Object.assign([], res)
		});
	}

	// 获取负责人列表
	@action
	getMasterList() {
		return requestPost('getMasterList', Config.bpm.getMasterList, Schemas.bpm.getMasterList).then((res) => {
			this.masterList = Object.assign([], res.map(item=>{
				return {text:item.userName, value:item.userName}
			}));
			this.masterIds =  Object.assign([], res.map(item=>{
				return Object.assign({}, {id:item.userId, typeName:item.userName})
			}));
		});
	}

	@observable personList=[];// 人员
	// 获取人员数据
	@action
	getUserInfoListByRoleCode() {
		return requestPost('getUserInfoListByRoleCode', Config.authorization.getUserInfoListByRoleCode, authorizationSchemas.authorization.getUserInfoListByRoleCode, {
			appId: "bocspace",
			code: "bocspace.group",
			pageIndex: 1,     // 暂时处理
			pageSize: 10000,    // 暂时这样处理
		}).then((res)=>{
			this.personList = Object.assign([], res.body);
		});
	}

	@observable projectList= []; //日历项目
	// 日历项目列表
	@action
	getCalendarProjectList() {
		return requestPost('getCalendarProjectList', Config.bpm.getCalendarProjectList, Schemas.bpm.getCalendarProjectList, {}).then((res)=>{
			this.projectList = Object.assign([], res);
		});
	}

	@observable yearMonth = ''; // 当前月份
	@observable personMonthTask = []
	// 人员列表
	@action
	getPersonMonthTaskState(param) {
		return requestPost('getPersonMonthTaskState', Config.bpm.getPersonMonthTaskState, Schemas.bpm.getPersonMonthTaskState, {
			yearMonth: this.yearMonth,
			userId: param
		}).then((res)=>{
			this.personMonthTask = Object.assign([], res);
		});
	}

	@observable projectStages = []; // 项目信息
	// 获取项目全部状态信息通过ids
	@action
	getProjectStagesByIds(param) {
		return requestPost('getProjectStagesByIds', Config.bpm.getProjectStagesByIds, Schemas.bpm.getProjectStagesByIds, {
			yearMonth: this.yearMonth,
			projectIds: param
		}).then((res)=>{
			this.projectStages = Object.assign([], res.data==null?[]:res.data);
		});
	}

	// 获取钉钉的部门
	@action
	getDepartment() {
		return requestPost('getDepartment', Config.authorization.getDepartment, authorizationSchemas.authorization.getDepartment, {
			appid: 'bocspace',
			pid: '1'
		});
	}

	// 获取钉钉部门的用户列表
	@action
	getUserInfoListByDingtalk(param) {
		return requestPost('getUserInfoListByDingtalk', Config.authorization.getUserInfoListByDingtalk, authorizationSchemas.authorization.getUserInfoListByDingtalk, {
			appid: 'bocspace',
			pageIndex: 1,
			pageSize: 100,
			depId: param
		});
	}
    // 获取全部部门与人员
	@action
	getDepartmentAndUsers(name) {
		return requestPost('getDepartmentAndUsers', Config.authorization.getDepartmentAndUsers, authorizationSchemas.authorization.getDepartmentAndUsers, {
			appid: 'bocspace',
			name: name
		});
	}

	@observable personParams = {  // 参数
		ids: [],
		beginTime: moment().startOf('day').valueOf(),
	    endTime: moment().endOf("day").valueOf()
	}
	@observable personResources = []; // 日程数据
	// 人员日程排期
	@action
	getPersonScheduleResources() {
		return requestPost('getPersonScheduleResources', Config.bpm.getPersonScheduleResources, Schemas.bpm.getPersonScheduleResources, Object.assign({}, this.personParams))
			  .then((res)=>{
				  this.personResources = Object.assign([], res.data==null?[]:res.data);
			  });
	}

	@observable state = [];
	// 获取产品类型
	@action
	getAllStages() {
		return requestPost('getAllStages', Config.bpm.getAllStages, Schemas.bpm.getAllStages).then((res) => {
			if (res) {
				this.state = Object.assign([], res.map((item)=> {
					return Object.assign({}, {
						id: item.id,
						typeName: item.stageName
					})
				}))
			}
		});
	}

	@observable ossData = {
		"securityToken": "",
		"accessKeyId": "",
		"accessKeySecret": "",
		"expiration": null
	}; // 阿里云上传参数

	// 获取大文件上传sts
	getSts() {
		return requestPost('getSts', Config.bpm.getSts, bpmSchemas.bpm.getSts, {})
		.then((res) => {
			let str = typeof res == "string" ? JSON.parse(res) : res;
			if (str) {
				this.ossData = Object.assign({}, str);
			}
		});
	}

	@observable weeksData = []; // 当年所有的周数据
	@observable activeweeks = {}; // 当前周
	@observable userId = ""; // 人员id
	@observable userName = ""; // 人员name
	@observable weekData = []; // 当前周数据
	@observable monthData = []; // 当前月数据
	@observable seasonData = []; // 当前季数据
	@observable yearData = []; // 当前年数据
	@observable defaultWeeksData = []; // 当前周数据默认
	@observable okrAtts = []; // okr附件
	@observable okrComments = []; // okr评论

	// 返回查询年的周数据
	getWeeks(param) {
		return requestPost('getWeeks', Config.okr.getWeeks, okrSchemas.okr.getWeeks, {
			year: param
		})
	}

    // 获取周明细
	getWeekDetails(param) {
		return requestPost('getWeekDetails', Config.okr.getWeekDetails, okrSchemas.okr.getWeekDetails, {
			code: param,
			userId: this.userId
		}).then((res)=>{
			this.weekData = Object.assign([], res);
			this.defaultWeeksData = Object.assign([], res);;
		});
	}

	// 获取月明细
	getMonthDetails(param) {
		return requestPost('getMonthDetails', Config.okr.getMonthDetails, okrSchemas.okr.getMonthDetails, {
			code: param,
			userId: this.userId
		}).then((res)=>{
			this.monthData = Object.assign([], res);
		});
	}

	// 获取季明细
	getSeasonDetails(param) {
		return requestPost('getSeasonDetails', Config.okr.getSeasonDetails, okrSchemas.okr.getSeasonDetails, {
			code: param,
			userId: this.userId
		}).then((res)=>{
			this.seasonData = Object.assign([], res);
		});
	}

	// 获取年明细
	getYearDetails(param) {
		return requestPost('getYearDetails', Config.okr.getYearDetails, okrSchemas.okr.getYearDetails, {
			code: param,
			userId: this.userId
		}).then((res)=>{
			this.yearData = Object.assign([], res);
		});
	}

	// 保存周明细
	saveWeekDetails() {
		return requestPost('saveWeekDetails', Config.okr.saveWeekDetails, okrSchemas.okr.saveWeekDetails, {
			list: this.weekData,
			code: this.activeweeks.id,
			userId: this.userId
		})
	}

	// 保存月明细
	saveMonthDetails() {
		return requestPost('saveMonthDetails', Config.okr.saveMonthDetails, okrSchemas.okr.saveMonthDetails,{
			list: this.monthData,
			code: this.activeweeks.month,
			userId: this.userId
		})
	}

	// 保存季明细
	saveSeasonDetails(param) {
		return requestPost('saveSeasonDetails', Config.okr.saveSeasonDetails, okrSchemas.okr.saveSeasonDetails, {
			list: this.seasonData,
			code: param,
			userId: this.userId
		})
	}

	// 保存年明细
	saveYearDetails(param) {
		return requestPost('saveYearDetails', Config.okr.saveYearDetails, okrSchemas.okr.saveYearDetails, {
			list: this.yearData,
			code: param,
			userId: this.userId
		})
	}

	// 获取评论的附件列表
	getAtts(params) {
		return requestPost('getAtts', Config.okr.getAtts, okrSchemas.okr.getAtts, {
			type: params.type,
			code: params.code,
			userId: this.userId
		}).then((res)=>{
			this.okrAtts = Object.assign([], res);
		});
	}

	// 获取评论内容
	getComments(params) {
		return requestPost('getComments', Config.okr.getComments, okrSchemas.okr.getComments, {
			type: params.type,
			code: params.code,
			userId: this.userId
		}).then((res)=>{
			this.okrComments = Object.assign([], res);
		});
	}

	// 保存附件
	saveAtt(params) {
		return requestPost('saveAtts', Config.okr.saveAtts, okrSchemas.okr.saveAtts, {
			type: params.type,
			code: params.code,
			userId: this.userId,
			attachments: params.attachments
		})
	}

	// 保存评论
	saveComment(params) {
		return requestPost('saveComment', Config.okr.saveComment, okrSchemas.okr.saveComment, {
			type: params.type,
			code: params.code,
			userId: this.userId,
			comment: params.comment
		})
	}

	// 删除附件
	delAtt(params) {
		return requestPost('delAtt', Config.okr.delAtt, okrSchemas.okr.delAtt, {
			type: params.type,
			code: params.code,
			userId: this.userId,
			attachmentId: params.attachmentId
		})
	}

	// 删除评论
	delComment(params) {
		return requestPost('delComment', Config.okr.delComment, okrSchemas.okr.delComment, {
			type: params.type,
			code: params.code,
			userId: this.userId,
			commentId: params.commentId
		})
	}
}

export default BpmStore;




