import React from 'react';
import { List, Spin, Icon, ConfigProvider, Tooltip } from 'antd';
import InfiniteScroll from 'react-infinite-scroller';
import moment from 'moment';

const dateFormat = 'YYYY-MM-DD';

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center', padding: '0' }}>
		<img
			width='130px'
			height="100px"
			src='./images/daiban_un.png' />
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)',
			paddingTop: '15px'
		}}>一切正常，给大家点赞！！！</p>
	</div>
);

let renderCount = 0;

export default class WarningList extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			loading: false,
			hasMore: true,
			pageIndex: 1,
		}
	}

	handleInfiniteOnLoad = () => {
		if (this.store.hasMore) {
			this.store.getWarnningList();
		}

		// this.store.getProjectPeopleInfoList(params).then((res) => {
		// 	if (res && res['body'].length > 0) {
		// 		let tempData = this.store.projectPeopleInfoList;
		// 		this.store.projectPeopleInfoList = tempData.concat(res['body'])
		// 		this.setState({
		// 			loading: false,
		// 			pageIndex: pageIndex + 1
		// 		});
		// 	} else {
		// 		this.setState({
		// 			hasMore: false
		// 		});
		// 	}

		// 	this.setState({
		// 		loading: false
		// 	});
		// });
	};

	getCurrentBell = (endTime, now) => {
		let currentBell = null;
		// const bell_wartergreen = {fontSize:"18px", color:"#00AAAA", marginRight:"10px"};
		const bell_yellow = { fontSize: "18px", color: "#e7b550", marginRight: "10px" };
		const bell_red = { fontSize: "18px", color: "#c4382c", marginRight: "10px" };
		const bell_blue = { fontSize: "18px", color: "#4da9de", marginRight: "10px" };

		let days = moment(now).diff(moment(endTime), 'days');
		if (days > 3) {
			currentBell = bell_red;
		} else if (days >= 2 && days <= 3) {
			currentBell = bell_yellow;
		} else {
			currentBell = bell_blue;
		}
		return currentBell;

	}

	getDealyDays = (endTime, now) => {
		let days = moment(now).diff(moment(endTime), 'days');
		return '延迟' + days + '天'
	}

	display5Items = () => {
		renderCount = renderCount + 1;
		if (renderCount == 9) {
			renderCount = 0;
			try {
				let warnningcontentDivClientHeight = document.getElementById('warnningcontent').clientHeight - 20;
				let warnningItemNeedHeight = Math.round(warnningcontentDivClientHeight / 5);
				let warnningItems = document.getElementsByName('warnningitem');
				setTimeout(() => {
					warnningItems.forEach(item => {
						item.style.height = (warnningItemNeedHeight) + "px";
						item.style.width = "100%";
						item.style.display = "table";
						item.style.borderBottom = "1px dashed #e8e8e8";
						item.style.paddingTop = "5px";
						item.style.paddingBottom = "0";

					})
				}, 5)
			} catch (error) {
				console.log(error)
			}
		}
	}



	render() {
		const { totalCount, data } = this.props;

		// this.display5Items();

		return (
			<div style={{ width: '100%', height: '100%', overflowY: 'hidden' }} >
				<div className="warning-container-header h">
					<div className="bpm_gray_label" style={data.length == 0 ? { "display": "none" } : { "width": "100%", height: '20px', lineHeight: '20px', tableLayout: "fixed", "display": "table" }}>
						<span style={{
							"width": "40%", "display": "table-cell", "textAlign": "center"
						}}>任务</span>
						<span style={{
							"width": "30%", "display": "table-cell", "textAlign": "center"
						}}>截止日期</span>
						<span style={{
							"width": "30%", "display": "table-cell", "textAlign": "center"
						}}>负责人</span>
					</div>
					<div id="warnningcontent" className={data.length == 0 ? "" : "warning-infinite-container"} ref={node => { this.container = node }} >
						<InfiniteScroll
							initialLoad={false}
							pageStart={0}
							loadMore={this.handleInfiniteOnLoad}
							hasMore={!this.state.loading && this.state.hasMore}
							useWindow={false}
						>
							<ConfigProvider renderEmpty={customizeRenderEmpty}>
								<List
									dataSource={data}
									renderItem={(item, index) => (
										<List.Item key={index} >
											<div name="warnningitem" style={{ "width": "100%", "display": "table",tableLayout: "fixed"}}>
												<span
													className="ellsis"
													style={{
													width: "40%",
													textAlign: "left",
													display: "table-cell",
													fontSize: "12px",
												}}
												      title={item.name}
												>
													<Tooltip placement="top" title={this.getDealyDays(item.endTime, item.now)}>
														<Icon style={this.getCurrentBell(item.endTime, item.now)} theme="filled" type="bell" /></Tooltip>
													{item.name}
												</span>
												<span
													className="ellsis"
													style={{
													width: "30%",
													textAlign: "center",
													display: "table-cell",
													paddingLeft: "4px",
													fontSize: "12px",
												}}>{item.endTime == 0 ? "" : moment(item.endTime).format(dateFormat)}</span>
												<span
													className="ellsis"
													style={{
													width: "30%",
													textAlign: "center",
													display: "table-cell",
													fontSize: "12px"
												}}>{typeof (item.masters) !== 'undefined' ? item.masters.map((obj) => { return obj.userName }).join('，') : ''}</span>
											</div>
										</List.Item>
									)}
								>
									{this.state.loading && this.state.hasMore && (
										<div className="warning-loading-container">
											<Spin />
										</div>
									)}
								</List>
							</ConfigProvider>
						</InfiniteScroll>
					</div>
				</div>
			</div>
		)
	}
}




