import React from 'react';
import {
	DatePicker,
	Select,
	Button,
	Tabs,
	Icon,
	Input,
} from 'antd';

import moment from 'moment';

import HeaderSearch from '../HeaderSearch';
import './index.css';

const { Search } = Input;
const { MonthPicker, RangePicker } = DatePicker;
const dateFormat = 'YYYY-MM-DD';

export default class SearchGroup extends React.Component {
	constructor(props) {
		super(props);
        this.state = {
            data:this.props.data,
            buttonToday:false,
            buttonWeek:false,
            buttonMonth:false,
            buttonYear:false,
            text:"",
            beginTime:moment().startOf('year').format(dateFormat),
            endTime:moment().endOf("year").format(dateFormat),
        }

    }


    componentWillReceiveProps(nextProps) {
        if(nextProps.data != this.props.data) {
           this.setState({data:nextProps.data})
        }
    }

    toggleTimeBucket=(type)=>{
        const {buttonToday, buttonWeek, buttonMonth, buttonYear, text, beginTime, endTime } = this.state;
        let obj = {};
        let otherObj = {};
        let timeObj = {};
        if(type==="today"){
            obj = {"buttonToday":!buttonToday}
            if(!buttonToday){
                otherObj = {"buttonWeek":false, "buttonMonth":false, "buttonYear":false};
            }
            timeObj = {"beginTime":moment().startOf('day').format(dateFormat), "endTime":moment().endOf('day').format(dateFormat) }
        }else if(type==="week"){
            obj = {"buttonWeek":!buttonWeek}
            if(!buttonWeek){
                otherObj = {"buttonToday":false, "buttonMonth":false, "buttonYear":false};
            }
            timeObj = {"beginTime":moment().startOf('week').format(dateFormat), "endTime":moment().endOf('week').format(dateFormat) }
        }else if(type==="month"){
            obj = {"buttonMonth":!buttonMonth}
            if(!buttonMonth){
                otherObj = {"buttonWeek":false, "buttonToday":false, "buttonYear":false};
            }
            timeObj = {"beginTime":moment().startOf('month').format(dateFormat), "endTime":moment().endOf('month').format(dateFormat) }
        }else if(type==="year"){
            obj = {"buttonYear":!buttonYear}
            if(!buttonYear){
                otherObj = {"buttonWeek":false, "buttonMonth":false, "buttonToday":false};
            }
            timeObj = {"beginTime":moment().startOf('year').format(dateFormat), "endTime":moment().endOf('year').format(dateFormat) }
        }
        this.setState(Object.assign(obj,otherObj,timeObj), ()=>{
            let parmas = {"text":text, "beginTime":this.state.beginTime, "endTime": this.state.endTime}
             // console.log("serarchparams"+JSON.stringify(parmas,null,2))
            this.onSearch(parmas);
        });

    }

    onChangeTimeRange=(dates, dateStrings)=>{
        let obj = {"buttonWeek":false, "buttonMonth":false, "buttonToday":false,"buttonYear":false};
        // console.log('From: ', dates[0], ', to: ', dates[1]);
        //console.log('From: ', dateStrings[0], ', to: ', dateStrings[1]);
        let timeObj = {"beginTime":dateStrings[0], "endTime":dateStrings[1] }
        this.setState(Object.assign(obj,timeObj));
    }

    onSearch =(parmas) => {
        this.props.onSearch(parmas)
    }

    render() {
        const { data, buttonToday, buttonWeek, buttonMonth, buttonYear, beginTime, endTime } = this.state;
		return (
			<div>
                {/* <Search visibilityToggle={true} className={'extraContentSearch search_hidden'}  /> */}
                <HeaderSearch
                    className={`action search`}
                    placeholder={""}
                    defaultValue=""
                    dataSource={[]}
                    onSearch={value => {
                    // console.log('input', value);
                    //  let parmas = {"text":value, "beginTime":beginTime, "endTime": endTime}
                    //  this.setState({"text":value});
                    //  this.onSearch(parmas)
                    }}
                    onPressEnter={value => {
                    //  console.log('enter', value);
                     let parmas = {"text":value, "beginTime":beginTime, "endTime": endTime}
                     this.setState({"text":value});
                     this.onSearch(parmas)
                    }}
                />
                <Button style={{padding: '5px'}} className={buttonToday? "": "btn_default_color"} onClick = {(e)=>this.toggleTimeBucket("today")} type={"link"}>今日</Button>
                <Button style={{padding: '5px'}} className={buttonWeek? "": "btn_default_color"} onClick = {(e)=>this.toggleTimeBucket("week")} type={"link"}>本周</Button>
                <Button style={{padding: '5px'}} className={buttonMonth? "": "btn_default_color"} onClick = {(e)=>this.toggleTimeBucket("month")} type={"link"}>本月</Button>
                <Button style={{padding: '5px 10px 5px 5px'}} className={buttonYear? "": "btn_default_color"} onClick = {(e)=>this.toggleTimeBucket("year")} type={"link"}>全年</Button>
                <RangePicker
                 style={{width: '220px'}}
                 className="w"
                 allowClear={false}
                 defaultValue={[moment(beginTime, dateFormat), moment(endTime, dateFormat)]}
                 value={[moment(beginTime, dateFormat), moment(endTime, dateFormat)]}
                 format={dateFormat}
                 onChange={this.onChangeTimeRange}
                />
			</div>
		)
	}
}



