import React from 'react';
import {observer} from 'mobx-react';
import {Link} from 'react-router-dom';
import {
	Affix,
	Breadcrumb,
	DatePicker,
	Select,
	Row,
	Col,
	Button,
	Table,
	Pagination,
	Form,
	Typography,
	Tabs,
	Icon,
	Input,
	ConfigProvider,
	Popconfirm,
	message,
	Anchor,
	Card
} from 'antd';
import moment from 'moment';

import {withRouter} from "react-router-dom"
import * as mobx from 'mobx';
import BpmStore from '@/store/bpm/BpmStore';
import SearchGroup from '@/components/bpm/SearchGroup';
import ProductCycle from '@/components/bpm/ProductCycle';
import RateCharts from '@/components/bpm/RateCharts';
import Top5List from '@/components/bpm/Top5List';
import WarningList from '@/components/bpm/WarningList';
import Progress from '@/components/bpm/Progress';
import DetailsList from '@/components/bpm/DetailsList';

const {TabPane} = Tabs;
const { Search } = Input;
const {Paragraph} = Typography;
const dateFormat = 'YYYY-MM-DD';

@withRouter
@observer
class ProgressList extends React.Component {
    constructor(props) {
        super(props);
        this.store = new BpmStore();
    }

    state = {
        activeKey: '0', // 激活的key,
    }

    componentWillMount() {
        this.loadPageInfo();
    }

    componentDidMount() {
        // 定时器
        this.timer = setInterval(() => {
            // 心跳接口 5分钟跳动一个
            this.loadPageInfo();
        }, 300000);
    }

    componentWillUnmount () {
        clearInterval(this.timer);
    }

    loadPageInfo = () =>{
        this.store.getProjectBoardList();
        this.store.getAllProjectList();
        this.store.getProductProportion();
        this.store.getTopContractAmountList();
        this.store.getDeliverRate(); //交付完成率
        this.store.getMoneyBackRate(); //回款完成率
        this.store.getMyTaskCount(); // 获取我的任务数
        // this.getMyRunningTaskNum();
        this.getDelayWarnningList();
        this.getProductTypeList();
        this.getMasterList();
        this.store.getAllStages();
    }

    getSearchResult = () => {
        this.store.getProjectBoardList();
        this.store.getAllProjectList();
    }

    getProductTypeList =() => {
        this.store.getProductTypeList();
    }
    getMasterList =() => {
        this.store.getMasterList();
    }

   /* getMyRunningTaskNum =() => {
        this.store.currentTaskParams.state = 2;
        this.store.getCurrentTaskList();
    }*/

    getDelayWarnningList =() => {
        this.store.getWarnningList();
    }

    tabsOnchange=(key)=>{
		this.setState({activeKey:key});
        // this.clearPageStatus();
    }

    onSearch = (parmas) =>{
        this.store.projectBoardParams.beginTime = moment(parmas.beginTime,dateFormat).valueOf();
        this.store.allProjectParams.beginTime = moment(parmas.beginTime,dateFormat).valueOf();

        this.store.projectBoardParams.endTime = moment(parmas.endTime,dateFormat).valueOf()+86399000; //时间差精确到当天的最后一秒
        this.store.allProjectParams.endTime = moment(parmas.endTime,dateFormat).valueOf()+86399000;   //时间差精确到当天的最后一秒

        this.store.projectBoardParams.text = parmas.text;
        this.store.allProjectParams.text = parmas.text;
        this.getSearchResult();
    }

    render() {
        const { activeKey } = this.state;
        const extraContent = (
			<SearchGroup onSearch={this.onSearch} />
        );

        return(
            <div className="w h">
                <Row className="w" style={{height: '66.6%'}}>
                    <Col id="tabpane" style={{height: '100%'}} span={18}>
                         <Tabs style={{padding: '0 5px'}} className="h bg_fff progress_tabs" tabBarExtraContent={extraContent} activeKey={activeKey} onChange={this.tabsOnchange}>
                            <TabPane
                                tab={<span>进展</span>}
                                className="h"
                                key="0">
                                <Progress store={this.store} data={this.store.projectBoardList}  />
                            </TabPane>
                            <TabPane
                                tab={<span>明细</span>}
                                className="h"
                                key="1">
                                <DetailsList  store={this.store} totalCount={this.store.allProjectTotal} data={this.store.allProjectList}  />
                            </TabPane>
                         </Tabs>
                    </Col>
                    <Col style={{height: '100%', paddingLeft: '5px'}} span={6}>
                        <Card bordered={false} title="进度延迟预警" className="bpm_card h" style={{height: '50%'}}>
                            <WarningList store={this.store} totalCount={this.store.currentTaskTotal} data={this.store.dealyWarnningList} />
                        </Card>
                        <Card bordered={false} title="交付完成率"  className="bpm_card mt5" style={{height: "calc(50% - 5px)"}}>
                         <RateCharts title="交付完成率" rateNum={this.store.deliverRate  || 0} />
                        </Card>
                    </Col>
                </Row>
                <Row className="w" style={{height: '33.3%', paddingTop: '5px'}}>
                    <Col style={{padding: '0', height: '100%'}} span={18}>
                        <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{height: '100%'}}>
                            <Card bordered={false} title="各产品占比" className="bpm_card h" >
                                <ProductCycle data={this.store.productProportion} />
                            </Card>
                        </Col>
                        <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{height: '100%', paddingLeft:'5px'}}>
                            <Card bordered={false} title="项目订单排名"  className="bpm_card h" >
                                <Top5List data={this.store.topContractAmount} />
                            </Card>
                        </Col>
                    </Col>
                    <Col style={{height: '100%', paddingLeft: '5px'}} span={6}>
                        <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{height: '100%'}}>
                        <Card bordered={false} title="回款完成率"  className="bpm_card h">
                            <RateCharts title="回款完成率" rateNum={this.store.moneyBackRate || 0} />
                        </Card>
                        </Col>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default ProgressList



